.custom-list {
  display: flex;
  justify-content: center;
  padding: 50px;
}
.custom-list .ant-list {
  width: 1080px;
  background-color: white;
}
.custom-list .ant-list .ant-list-item-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.custom-list .ant-list .ant-list-item-content .left-section {
  display: flex;
  align-items: center;
}
.custom-list .ant-list .ant-list-item-content .left-section .list-item-info {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
}
.custom-list .ant-list .ant-list-item-content .left-section .list-item-info span + span {
  margin-left: 24px;
}
