.config-panel {
  border-left: 1px solid #e8e8e8;
}
.config-panel .ant-layout-sider-children {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.config-panel .ant-layout-sider-children .title {
  line-height: 48px;
}
.config-panel .ant-layout-sider-children .config-item {
  width: 160px;
}
.config-panel .ant-layout-sider-children .config-item + .config-item {
  margin-top: 24px;
}
