.user-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.user-header .logo {
  height: 64px;
  cursor: pointer;
}
.user-header .right-section {
  display: flex;
  align-items: center;
}
.user-sider {
  border-right: 1px solid #e8e8e8;
}
.user-content {
  background-color: #fafafa !important;
}
